.row {
  margin-left: 20px;
}
.row_posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding-bottom: 50px;
  padding: 20px;
}
.row_posters::-webkit-scrollbar {
  display: none;
}
.row_poster {
  object-fit: contain;
  size: fixed;
  max-height: 100px;
  width: 100%;
  margin-right: 10px;
  transform: 450ms;
}
.row_poster:hover {
  transform: scale(1.08);
  opacity: 1;
}
.rowposter_large {
  max-height: 250px;
  margin-right: 10px;
}
.rowposter_large :hover {
  transform: scale(1.09);
}
.label-movie {
  color: white;
}
