.nav {
  position: fixed;
  top: 0;
  margin-right: 30px;
  display: flex;
  padding: 20px;
  width: 100%;
  height: 30px;
  justify-content: space-between;
  transition-timing-function: ease-in;
  transition: all 0.5s;
}
.netflix-logo {
  width: 100px;
  left: 20px;
  object-fit: contain;
}
.netflix-button {
  cursor: pointer;
  background-color: red;
  color: white;
  border: none;
  width: 75px;
  outline: none;
  border-radius: 4px;
  font-weight: 700;
  margin-right: 50px;
}

.netflix-button:hover {
  color: black;

  background-color: white;
}

.nav_black {
  background-color: #111111;
}
